















































import Vue from "vue";
import BScroll from "better-scroll";

import { mapActions, mapState, mapGetters, mapMutations } from "vuex";
export default Vue.extend({
  name: "home",
  components: {},
  data() {
    return {
      mask: false,
      currentIndex: 0,
      isTouch: false
    };
  },
  computed: {
    ...mapState({
      list: (state: any) => state.home.list,
      maskList: (state: any) => state.home.maskList
    }),
    current(state: any): any {
      return Object.keys(this.list)[this.currentIndex];
    }
  },
  methods: {
    ...mapActions({
      getList: "home/getList",
      getDetail: "home/getDetail"
    }),
    //楼层
    toList(ind: number) {
      console.log("tap");
      // this.current = Object.keys(this.list)[ind];
      this.bscroll.scrollTo(0, -this.$refs.tit[ind].offsetTop);
    },
    //弹抽屉
    toMask(id: string) {
      this.mask = true;
      this.getDetail(id);
    },
    //右滑
    right() {
      this.mask = false;
    },
    touchTo(e) {
      console.log(e);

      let pageY = e.touches[0].pageY;
      let letterHeight = ((0.4 * window.innerWidth) / 750) * 100;
      // 0.4为li的高度
      let letterOffsetTop =
        (window.innerHeight - letterHeight * Object.keys(this.list).length) / 2;
      let letterIndex = Math.floor((pageY - letterOffsetTop) / letterHeight);
      // 处理上边界
      if (letterIndex < 0) {
        letterIndex = 0;
      }
      // 处理下边界
      if (letterIndex > Object.keys(this.list).length - 1) {
        letterIndex = Object.keys(this.list).length - 1;
      }
      console.log({
        pageY,
        innerWidth: window.innerWidth,
        letterHeight,
        letterOffsetTop,
        letterIndex
      });

      this.currentIndex = letterIndex;
    },
    //滑过列表
    touchStart(e: Event): void {
      this.isTouch = true;
      this.touchTo(e);
    },
    touchMove(e: any): void {
      this.touchTo(e);
      this.toList(this.currentIndex);
    },
    touchEnd(e: Event): void {
      this.isTouch = false;
    },
    toCar(SerialID: string) {
      this.$router.push({
        name: "car",
        params: {
          id: SerialID
        }
      });
    },
    async getScrollList() {
      await this.getList();
      this.$nextTick(() => {
        this.bscroll = new BScroll(this.$refs.wrap, {
          probeType: 2,
          tap: true
        });
      });
    }
  },
  created() {
    this.getScrollList();
  },
  updated() {
    // this.bscroll.refresh();
  }
});
